body {
    margin: 0;
}

.vertical-tabs-container {
  $base-border-color: gainsboro !default;
  $base-border-radius: 3px !default;
  $base-background-color: white !default;
  $base-line-height: 1.5em !default;
  $base-spacing: 1.5em !default;
  $dark-gray: #333 !default;
  $medium-screen: 40em !default;
  $base-font-color: $dark-gray !default;
  $tab-border-color: $base-border-color;
  $tab-border: 1px solid $tab-border-color;
  $tab-border-radius: $base-border-radius;
  $tab-content-background: #F9F9F9;
  $tab-active-color: $tab-content-background;
  $tab-inactive-color: $base-background-color;
  $vertical-tabs-height: 658px;
  $vertical-tab-mode: $medium-screen;

  @include clearfix;
  border: $tab-border;
  border-radius: $tab-border-radius;
  margin-bottom: $base-spacing;
  overflow: hidden;

  li {
	list-style: none;
  }

  a {
	color: $action-color;
	text-decoration: none;
  }

  .vertical-tabs {
	display: none;

	@include media($vertical-tab-mode) {
	  background-color: $tab-inactive-color;
	  display: inline;
	  float: left;
	  height: $vertical-tabs-height;
	  width: 30%;
	}
  }

  .vertical-tab {
	@include media($vertical-tab-mode) {
	  border-bottom: $tab-border;
	  display: block;
	  font-weight: bold;
	  margin-right: -1px;
	  padding: ($base-spacing / 2) ($gutter / 2);

	  &.is-active {
		background-color: $tab-content-background;
		margin-right: -1px;
	  }
	}
  }

  a.vertical-tab-accordion-heading,
  a.vertical-tab {
	color: $dark-gray;
  }

  .vertical-tab:focus {
	outline: none;
  }

  .vertical-tab-content-container {
	display: block;
	margin: 0 auto;

	& a:focus {
	  outline: none;
	}

	@include media($vertical-tab-mode) {
        @include size(70%, $vertical-tabs-height);
	  background-color: $tab-content-background;
	  display: inline-block;
	}
  }

  .vertical-tab-content {
	background-color: $tab-content-background;
	padding: $base-spacing $gutter;

	p {
	  color: $base-font-color;
	  line-height: $base-line-height;
	}

	@include media($vertical-tab-mode) {
	  border: 0;
	  display: none;
	}
  }

  .vertical-tab-accordion-heading {
	background-color: $tab-inactive-color;
	border-top: $tab-border;
	cursor: pointer;
	display: block;
	font-weight: bold;
	padding: $base-spacing / 2 $gutter / 2;

	&:focus,
	&:hover {
	  color: $action-color;
	}

	&:first-child {
	  border-top: 0;
	}

	&.is-active {
	  background: $tab-active-color;
	  border-bottom: 0;
	}

	@include media($vertical-tab-mode) {
	  display: none;
	}
  }
}

.hero-intro {
    min-height: 75vh;
    background: #B0976D;
    color: white;
    position: relative;
    h1 {
        font-size: 2.65rem;
        text-transform: uppercase;
        margin-bottom: 10px;
    }

    .copy {
        left: 50%;
        margin: auto;
        position: absolute;
        text-align: center;
        top: 50%;
        transform: translate(-50%, -50%);
        z-index: 999;

        p {
            color: white;
            line-height: 1.5em;
            padding: 1em 2em;
            position: relative;
        }
    }
}

.meta-forms-wrapper {
    @include clearfix();
    margin: 60px 40px;
    .meta-forms {
        @include media($large-screen) {
            @include span-columns(6); 
        }
    }
    .meta-tags-output {
        @include media($large-screen) {
            @include span-columns(6); 
        }

        pre {
            margin-top: 0;
        }
    }
}
pre, code {
    background: #3C3C3C;
    border-radius: 4px;
    color: #4ca4ca;
    color: #B0976D;
    font-family: "Roboto Mono";
    font-size: 0.8em;
    line-height: 1.6;
    padding: 10px 10px 10px 10px;
    white-space: pre-wrap;
    white-space: -moz-pre-wrap;
    white-space: -pre-wrap;
    white-space: -o-pre-wrap;
    word-wrap: break-word;
}

.text-desc {
    font-size: 0.8em;
    margin-bottom: 10px;
    font-weight: 400;
    display: block;
}

.light-text {
    font-weight: 400;
}

footer {
    background: #B0976D;
    color: white;
    padding: 20px 40px;
    a {
        color: white;
        text-decoration: underline;
    }
    p {
        margin: 0;
    }
}
